import React, { Component } from 'react';
import classes from './App.module.scss';
import Slider from '../../components/Slider/Slider';
import NavBar from '../../components/NavBar/NavBar';
import appRoutes from '../../routes/App';
import Router from '../../components/UI/Router/Router';
import SideDrawer from '../../components/SideDrawer/SideDrawer';
import Backdrop from '../../components/UI/Backdrop/Backdrop';
import Footer from '../../components/Footer/Footer';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      showSideDrawer: false
    }
  }
  toggleSideDrawer = () => {
    this.setState({
      ...this.state,
      showSideDrawer: !this.state.showSideDrawer
    });
  }
  render() {
    const { showSideDrawer } = this.state;
    let backdrop, sideDrawer;
    if (showSideDrawer) {
      sideDrawer = <SideDrawer routes={appRoutes} />
      backdrop = <Backdrop onClick={this.toggleSideDrawer} />
    }
    return (
      <div className={classes.app}>
        {backdrop}
        {sideDrawer}
        <Slider />
        <NavBar routes={appRoutes} onMenuClick={this.toggleSideDrawer} />
        <main >
          <Router routes={appRoutes} />
        </main>
        <Footer />
      </div>
    );
  }
}

export default App;
