import React from 'react'
import PropTypes from 'prop-types';
import classes from './NavBar.module.scss';
import { NavLink } from 'react-router-dom';
import IconButton from '../UI/IconButton/IconButton';
import { ReactComponent as FacebookLogo } from '../../assets/icons/facebook-app-symbol.svg';
import { ReactComponent as InstagramLogo } from '../../assets/icons/instagram-logo.svg';
function NavBar(props) {
    const { routes,onMenuClick } = props;
    return (
        <header className={classes.root}>
            <IconButton className={classes.menuIcon} onClick={onMenuClick}>
                <FacebookLogo />
            </IconButton>
            <div className={classes.brand}>
                DP - Destino pacífico
            </div>
            <nav className={classes.nav}>
                {routes.filter(_route => !Boolean(_route.redirect)).map(_route =>
                    <NavLink key={_route.path} to={_route.path} activeClassName={classes.active} exact >{_route.label}</NavLink>
                )}
            </nav>
            <div className={classes.social}>
                <IconButton className={classes.button}>
                    <FacebookLogo />
                </IconButton>
                <IconButton className={classes.button}>
                    <InstagramLogo />
                </IconButton>
            </div>
        </header>
    )
}

NavBar.propTypes = {
    title: PropTypes.string,
    onMenuClick: PropTypes.func.isRequired,
    routes: PropTypes.array.isRequired
}

export default NavBar

